  setup(props, { emit }) {
    useForm();
    function onSubmit(values) {
      emit("onSubmit", values);
    }
    const configObject = reactive({
      batchNo: props.configObj,
      PasswordTextBox: props.configObj.componentProps.PasswordTextBox,
      AuthorizeButton: props.configObj.componentProps.AuthorizeButton,
      UserNameTextBox: props.configObj.componentProps.UserNameTextBox,
      BackButton: props.configObj.componentProps.BackButton,
      OkButton: props.configObj.componentProps.OkButton,
      ProgressBar: props.configObj.componentProps.ProgressBar
        ? props.configObj.componentProps.ProgressBar
        : props.configObj.componentProps,
      Section1: props.configObj.componentProps.section1,
      Section2: props.configObj.componentProps.section2,
      Section3: props.configObj.componentProps.section3,
    });

    return {
      onSubmit,
      configObject,
      PasswordTextBox: {
        spanInput: ref(14), // this property to be set on screen level
        spanLabel: ref(10), // this property to be set on screen level
