        spanInput: ref(24), // this property to be set on screen level
      },

      BackButton: {
        nativeType: "button", // this property to be set on screen level
        spanInput: ref(24), // this property to be set on screen level
      },
      ProgressBar:{
        textInside: false,
        strokeWidth: 26,
        percentage: 0,
        color: 'rgb(0, 155, 131)'
      }

      ///////////////////// hide show sections of screen ///////////
    };
